import React, { useState, useCallback } from 'react';
import { useDropzone } from 'react-dropzone';
import { PhotoIcon, CameraIcon, DocumentIcon } from '@heroicons/react/24/outline';
import toast from 'react-hot-toast';

interface FileUploaderProps {
  onFileSelect: (file: File, fileUrl: string) => void;
  onUploadComplete: (result: any) => void;
  uploading: boolean;
}

const FileUploader: React.FC<FileUploaderProps> = ({ onFileSelect, onUploadComplete, uploading }) => {
  const [preview, setPreview] = useState<string | null>(null);
  const [fileType, setFileType] = useState<'image' | 'pdf' | null>(null);

  const handleFileUpload = useCallback(async (file: File) => {
    try {
      const formData = new FormData();
      formData.append('file', file);
      
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      const result = await response.json();

      if (result.success) {
        onUploadComplete(result);
        toast.success('文件上传成功！');
      } else {
        toast.error(result.error || '文件上传失败');
      }
    } catch (error) {
      console.error('文件上传错误:', error);
      toast.error('文件上传失败');
    }
  }, [onUploadComplete]);

  const onDrop = useCallback((acceptedFiles: File[]) => {
    if (acceptedFiles.length > 0) {
      const file = acceptedFiles[0];
      const fileUrl = URL.createObjectURL(file);
      
      // 设置预览
      if (file.type.startsWith('image/')) {
        setPreview(fileUrl);
        setFileType('image');
      } else if (file.type === 'application/pdf') {
        setPreview(null);
        setFileType('pdf');
      }
      
      onFileSelect(file, fileUrl);
      handleFileUpload(file);
    }
  }, [onFileSelect, handleFileUpload]);

  // 相机拍摄
  const handleCameraCapture = useCallback(async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: { facingMode: 'environment' }
      });
      
      const video = document.createElement('video');
      video.srcObject = stream;
      video.play();
      
      const modal = document.createElement('div');
      modal.className = 'fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50';
      
      const container = document.createElement('div');
      container.className = 'bg-white p-4 rounded-lg max-w-sm w-full mx-4';
      
      const canvas = document.createElement('canvas');
      const captureBtn = document.createElement('button');
      captureBtn.textContent = '拍摄';
      captureBtn.className = 'w-full mt-4 bg-primary-600 text-white py-2 px-4 rounded-lg hover:bg-primary-700';
      
      const closeBtn = document.createElement('button');
      closeBtn.textContent = '关闭';
      closeBtn.className = 'w-full mt-2 bg-gray-300 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-400';
      
      container.appendChild(video);
      container.appendChild(captureBtn);
      container.appendChild(closeBtn);
      modal.appendChild(container);
      document.body.appendChild(modal);
      
      captureBtn.onclick = () => {
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        const ctx = canvas.getContext('2d');
        ctx?.drawImage(video, 0, 0);
        
        canvas.toBlob((blob) => {
          if (blob) {
            const file = new File([blob], 'camera-capture.jpg', { type: 'image/jpeg' });
            const fileUrl = URL.createObjectURL(file);
            setPreview(fileUrl);
            setFileType('image');
            onFileSelect(file, fileUrl);
            handleFileUpload(file);
          }
        }, 'image/jpeg', 0.8);
        
        stream.getTracks().forEach(track => track.stop());
        document.body.removeChild(modal);
      };
      
      closeBtn.onclick = () => {
        stream.getTracks().forEach(track => track.stop());
        document.body.removeChild(modal);
      };
      
    } catch (error) {
      console.error('相机访问失败:', error);
      toast.error('无法访问相机，请检查权限设置');
    }
  }, [onFileSelect, handleFileUpload]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.gif', '.bmp', '.webp'],
      'application/pdf': ['.pdf']
    },
    maxFiles: 1,
    disabled: uploading
  });

  const clearFile = () => {
    setPreview(null);
    setFileType(null);
  };

  return (
    <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
      <div className="p-4 border-b border-gray-200">
        <h3 className="text-lg font-medium text-gray-900">文件上传</h3>
        <p className="text-sm text-gray-500 mt-1">
          支持图片格式（JPG、PNG、GIF等）和PDF文件
        </p>
      </div>

      <div className="p-4">
        {/* 文件上传区域 */}
        <div
          {...getRootProps()}
          className={`border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors ${
            isDragActive
              ? 'border-primary-300 bg-primary-50'
              : 'border-gray-300 hover:border-primary-400'
          } ${uploading ? 'opacity-50 cursor-not-allowed' : ''}`}
        >
          <input {...getInputProps()} />
          
          {preview && fileType === 'image' ? (
            <div className="space-y-4">
              <img
                src={preview}
                alt="Preview"
                className="max-w-full max-h-48 mx-auto rounded-lg shadow-sm"
              />
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  clearFile();
                }}
                className="text-sm text-gray-500 hover:text-gray-700"
              >
                重新选择文件
              </button>
            </div>
          ) : fileType === 'pdf' ? (
            <div className="space-y-4">
              <DocumentIcon className="mx-auto h-16 w-16 text-red-500" />
              <p className="text-sm text-gray-900 font-medium">PDF文件已选择</p>
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  clearFile();
                }}
                className="text-sm text-gray-500 hover:text-gray-700"
              >
                重新选择文件
              </button>
            </div>
          ) : (
            <div className="space-y-4">
              <div className="flex justify-center space-x-4">
                <PhotoIcon className="h-12 w-12 text-gray-400" />
                <DocumentIcon className="h-12 w-12 text-gray-400" />
              </div>
              {uploading ? (
                <div>
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600 mx-auto"></div>
                  <p className="text-sm text-gray-500 mt-2">正在上传...</p>
                </div>
              ) : isDragActive ? (
                <p className="text-sm text-primary-600">释放文件到此处...</p>
              ) : (
                <div>
                  <p className="text-sm text-gray-900 font-medium">
                    点击选择文件或拖拽到此处
                  </p>
                  <p className="text-xs text-gray-500 mt-1">
                    支持 JPG、PNG、GIF、PDF 等格式，最大10MB
                  </p>
                </div>
              )}
            </div>
          )}
        </div>

        {/* 相机拍摄按钮 */}
        {!preview && (
          <div className="mt-4">
            <button
              onClick={handleCameraCapture}
              disabled={uploading}
              className="w-full flex items-center justify-center space-x-2 px-4 py-2 border border-gray-300 rounded-lg text-sm text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              <CameraIcon className="h-4 w-4" />
              <span>使用相机拍摄</span>
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default FileUploader; 