import React, { useState, useEffect, useCallback } from 'react';
import Tesseract, { RecognizeResult } from 'tesseract.js';
import { toast } from 'react-hot-toast';
import { RecognizedData, OrderItem } from '../types';

interface OCRProcessorProps {
  image: File;
  onOCRComplete: (data: RecognizedData) => void;
  onProcessingStart: () => void;
  onProcessingEnd: () => void;
  extractedText?: string;
  fileType?: string;
}

const OCRProcessor: React.FC<OCRProcessorProps> = ({
  image,
  onOCRComplete,
  onProcessingStart,
  onProcessingEnd,
  extractedText,
  fileType
}) => {
  const [ocrText, setOcrText] = useState<string>('');
  const [ocrProgress, setOcrProgress] = useState<number>(0);
  const [isProcessing, setIsProcessing] = useState<boolean>(false);
  const [parsedData, setParsedData] = useState<RecognizedData | null>(null);
  const [editableText, setEditableText] = useState<string>('');

  // 执行OCR识别
  const performOCR = useCallback(async () => {
    // 如果是PDF且已有提取的文本，直接使用
    if (fileType === 'application/pdf' && extractedText) {
      setOcrText(extractedText);
      setEditableText(extractedText);
      parseOrderData(extractedText);
      return;
    }

    // 对图片进行OCR识别
    if (!fileType?.startsWith('image/')) {
      return;
    }

    setIsProcessing(true);
    onProcessingStart();
    setOcrProgress(0);

    try {
      const worker = await Tesseract.createWorker('chi_sim+eng', 1, {
        logger: m => {
          if (m.status === 'recognizing text') {
            setOcrProgress(Math.round(m.progress * 100));
          }
        }
      });

      const { data: { text } } = await worker.recognize(image);
      
      await worker.terminate();
      
      setOcrText(text);
      setEditableText(text);
      parseOrderData(text);
      
      toast.success('OCR识别完成！');
    } catch (error) {
      console.error('OCR错误:', error);
      toast.error('OCR识别失败，请重试');
    } finally {
      setIsProcessing(false);
      onProcessingEnd();
    }
  }, [image, onProcessingStart, onProcessingEnd, extractedText, fileType]);

  // 解析订单数据
  const parseOrderData = useCallback((text: string) => {
    try {
      const lines = text.split('\n').map(line => line.trim()).filter(line => line.length > 0);
      
      // 初始化订单数据
      let orderNumber = '';
      let supplier = '';
      let orderDate = '';
      let deliveryDate = '';
      const items: OrderItem[] = [];
      let totalAmount = 0;

      // 正则表达式模式
      const orderNumberPattern = /(?:订单号|单号|编号|NO|Order)[\s:：]*([A-Za-z0-9\-_]+)/i;
      const supplierPattern = /(?:供应商|厂商|公司|Supplier)[\s:：]*([^\n]+)/i;
      const datePattern = /(\d{4}[-\/年]\d{1,2}[-\/月]\d{1,2}[日]?)/g;
      const pricePattern = /(?:￥|¥|\$|USD|RMB)?\s*(\d+(?:,\d{3})*(?:\.\d{2})?)/g;

      // 提取订单号
      const orderMatch = text.match(orderNumberPattern);
      if (orderMatch) {
        orderNumber = orderMatch[1];
      }

      // 提取供应商
      const supplierMatch = text.match(supplierPattern);
      if (supplierMatch) {
        supplier = supplierMatch[1];
      }

      // 提取日期
      const dates = text.match(datePattern);
      if (dates && dates.length > 0) {
        orderDate = dates[0];
        if (dates.length > 1) {
          deliveryDate = dates[1];
        }
      }

      // 简单的商品行识别（基于行数据结构）
      let foundTableStart = false;
      for (let i = 0; i < lines.length; i++) {
        const line = lines[i];
        
        // 寻找表格开始
        if (!foundTableStart && (
          line.includes('产品') || line.includes('商品') || line.includes('名称') ||
          line.includes('数量') || line.includes('单价') || line.includes('金额') ||
          line.includes('Product') || line.includes('Item') || line.includes('Quantity') ||
          line.includes('Price') || line.includes('Amount')
        )) {
          foundTableStart = true;
          continue;
        }

        if (foundTableStart && line.length > 10) {
          // 尝试从行中提取商品信息
          const prices = line.match(pricePattern);
          const numbers = line.match(/\d+/g);
          
          if (prices && numbers) {
            const productName = line.split(/\d/)[0].trim();
            const quantity = parseInt(numbers[0]) || 1;
            const unitPrice = parseFloat(prices[0].replace(/[￥¥$,]/g, '')) || 0;
            const totalPrice = quantity * unitPrice;

            if (productName && productName.length > 2) {
              items.push({
                productName,
                quantity,
                unitPrice,
                totalPrice,
                specifications: '',
                remarks: ''
              });
            }
          }
        }
      }

      // 计算总金额
      totalAmount = items.reduce((sum, item) => sum + item.totalPrice, 0);

      // 如果没有找到具体的商品信息，创建一个默认项
      if (items.length === 0) {
        items.push({
          productName: '待编辑商品',
          quantity: 1,
          unitPrice: 0,
          totalPrice: 0,
          specifications: '',
          remarks: '请手动编辑商品信息'
        });
      }

      const recognizedData: RecognizedData = {
        orderNumber: orderNumber || `ORDER_${Date.now()}`,
        supplier: supplier || '待编辑供应商',
        orderDate: orderDate || new Date().toLocaleDateString(),
        deliveryDate: deliveryDate || '',
        items,
        totalAmount,
        rawText: text
      };

      setParsedData(recognizedData);
      onOCRComplete(recognizedData);
    } catch (error) {
      console.error('解析错误:', error);
      toast.error('数据解析失败，请检查识别结果');
    }
  }, [onOCRComplete]);

  // 手动处理编辑后的文本
  const handleTextReparse = useCallback(() => {
    if (editableText.trim()) {
      parseOrderData(editableText);
      toast.success('文本已重新解析！');
    }
  }, [editableText, parseOrderData]);

  // 自动开始OCR
  useEffect(() => {
    if (image && !isProcessing && !ocrText) {
      performOCR();
    }
  }, [image, isProcessing, ocrText, performOCR]);

  // 手动编辑数据
  const handleDataEdit = useCallback((field: string, value: any) => {
    if (parsedData) {
      const updatedData = { ...parsedData, [field]: value };
      setParsedData(updatedData);
      onOCRComplete(updatedData);
    }
  }, [parsedData, onOCRComplete]);

  const handleItemEdit = useCallback((index: number, field: string, value: any) => {
    if (parsedData) {
      const updatedItems = [...parsedData.items];
      updatedItems[index] = { ...updatedItems[index], [field]: value };
      
      // 重新计算总价
      if (field === 'quantity' || field === 'unitPrice') {
        updatedItems[index].totalPrice = updatedItems[index].quantity * updatedItems[index].unitPrice;
      }

      const totalAmount = updatedItems.reduce((sum, item) => sum + item.totalPrice, 0);
      const updatedData = { ...parsedData, items: updatedItems, totalAmount };
      
      setParsedData(updatedData);
      onOCRComplete(updatedData);
    }
  }, [parsedData, onOCRComplete]);

  const addItem = useCallback(() => {
    if (parsedData) {
      const newItem: OrderItem = {
        productName: '新商品',
        quantity: 1,
        unitPrice: 0,
        totalPrice: 0,
        specifications: '',
        remarks: ''
      };
      
      const updatedItems = [...parsedData.items, newItem];
      const updatedData = { ...parsedData, items: updatedItems };
      
      setParsedData(updatedData);
      onOCRComplete(updatedData);
    }
  }, [parsedData, onOCRComplete]);

  const deleteItem = useCallback((index: number) => {
    if (parsedData && parsedData.items.length > 1) {
      const updatedItems = parsedData.items.filter((_, i) => i !== index);
      const totalAmount = updatedItems.reduce((sum, item) => sum + item.totalPrice, 0);
      const updatedData = { ...parsedData, items: updatedItems, totalAmount };
      
      setParsedData(updatedData);
      onOCRComplete(updatedData);
    }
  }, [parsedData, onOCRComplete]);

  return (
    <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
      <div className="p-4 border-b border-gray-200">
        <div className="flex items-center justify-between">
          <h3 className="text-lg font-medium text-gray-900">内容识别</h3>
          {!isProcessing && ocrText && (
            <button
              onClick={performOCR}
              className="text-sm text-blue-600 hover:text-blue-700 transition-colors"
            >
              重新识别
            </button>
          )}
        </div>
      </div>

      <div className="p-4">
        {/* 文件类型和进度显示 */}
        {fileType && (
          <div className="mb-4 p-3 bg-gray-50 rounded-lg">
            <p className="text-sm text-gray-700">
              <span className="font-medium">文件类型：</span>
              {fileType === 'application/pdf' ? 'PDF文档' : '图片文件'}
            </p>
            {fileType === 'application/pdf' && extractedText && (
              <p className="text-sm text-green-600 mt-1">✓ PDF文本已自动提取</p>
            )}
          </div>
        )}

        {/* OCR进度条 */}
        {isProcessing && (
          <div className="mb-6">
            <div className="flex justify-between items-center mb-2">
              <span className="text-sm text-gray-600">正在识别...</span>
              <span className="text-sm text-gray-600">{ocrProgress}%</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${ocrProgress}%` }}
              ></div>
            </div>
          </div>
        )}

        {/* 原始文本编辑 */}
        {ocrText && (
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              识别文本（可编辑）
            </label>
            <textarea
              value={editableText}
              onChange={(e) => setEditableText(e.target.value)}
              className="w-full h-32 p-3 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              placeholder="识别的文本内容..."
            />
            <button
              onClick={handleTextReparse}
              className="mt-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
            >
              重新解析
            </button>
          </div>
        )}

        {/* 解析后的数据编辑 */}
        {parsedData && (
          <div className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">订单号</label>
                <input
                  type="text"
                  value={parsedData.orderNumber}
                  onChange={(e) => handleDataEdit('orderNumber', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">供应商</label>
                <input
                  type="text"
                  value={parsedData.supplier}
                  onChange={(e) => handleDataEdit('supplier', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">订单日期</label>
                <input
                  type="date"
                  value={parsedData.orderDate}
                  onChange={(e) => handleDataEdit('orderDate', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">交付日期</label>
                <input
                  type="date"
                  value={parsedData.deliveryDate}
                  onChange={(e) => handleDataEdit('deliveryDate', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
              </div>
            </div>

            {/* 商品列表 */}
            <div>
              <div className="flex items-center justify-between mb-3">
                <label className="block text-sm font-medium text-gray-700">商品清单</label>
                <button
                  onClick={addItem}
                  className="text-sm text-blue-600 hover:text-blue-700 transition-colors"
                >
                  + 添加商品
                </button>
              </div>
              
              <div className="space-y-3">
                {parsedData.items.map((item, index) => (
                  <div key={index} className="p-3 border border-gray-200 rounded-lg">
                    <div className="grid grid-cols-1 md:grid-cols-4 gap-3">
                      <div>
                        <label className="block text-xs text-gray-500 mb-1">商品名称</label>
                        <input
                          type="text"
                          value={item.productName}
                          onChange={(e) => handleItemEdit(index, 'productName', e.target.value)}
                          className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-1 focus:ring-blue-500"
                        />
                      </div>
                      <div>
                        <label className="block text-xs text-gray-500 mb-1">数量</label>
                        <input
                          type="number"
                          value={item.quantity}
                          onChange={(e) => handleItemEdit(index, 'quantity', parseFloat(e.target.value) || 0)}
                          className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-1 focus:ring-blue-500"
                        />
                      </div>
                      <div>
                        <label className="block text-xs text-gray-500 mb-1">单价</label>
                        <input
                          type="number"
                          step="0.01"
                          value={item.unitPrice}
                          onChange={(e) => handleItemEdit(index, 'unitPrice', parseFloat(e.target.value) || 0)}
                          className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-1 focus:ring-blue-500"
                        />
                      </div>
                      <div className="flex items-end space-x-2">
                        <div className="flex-1">
                          <label className="block text-xs text-gray-500 mb-1">小计</label>
                          <div className="p-2 bg-gray-50 rounded text-sm font-medium">
                            ¥{item.totalPrice.toFixed(2)}
                          </div>
                        </div>
                        <button
                          onClick={() => deleteItem(index)}
                          className="p-2 text-red-500 hover:text-red-700 transition-colors"
                          disabled={parsedData.items.length <= 1}
                        >
                          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                          </svg>
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              <div className="mt-4 p-3 bg-blue-50 rounded-lg">
                <div className="flex justify-between items-center">
                  <span className="font-medium text-gray-900">总金额：</span>
                  <span className="text-lg font-bold text-blue-600">
                    ¥{parsedData.totalAmount.toFixed(2)}
                  </span>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* 未开始识别的提示 */}
        {!ocrText && !isProcessing && (
          <div className="text-center py-12 text-gray-500">
            <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
            </svg>
            <p className="text-lg font-medium mb-2">
              {fileType === 'application/pdf' ? '准备处理PDF文档' : '准备识别文件内容'}
            </p>
            <p className="text-sm">
              {fileType === 'application/pdf' 
                ? '系统将自动提取PDF中的文本内容'
                : '系统将使用OCR技术识别图片中的文字'
              }
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default OCRProcessor; 