import React, { useState, useCallback, useEffect, useRef } from 'react';
import { useDropzone } from 'react-dropzone';
import { 
  DocumentArrowUpIcon, 
  TrashIcon, 
  DocumentTextIcon, 
  CheckCircleIcon,
  PhotoIcon,
  RectangleStackIcon,
  ArrowRightIcon,
  PlusIcon
} from '@heroicons/react/24/outline';
import toast from 'react-hot-toast';

interface Template {
  filename: string;
  originalname: string;
  size: number;
  uploadTime: string;
  isDefault?: boolean;
  inputFile?: {
    filename: string;
    originalname: string;
    type: string;
    url: string;
  };
  outputFile?: {
    filename: string;
    originalname: string;
    type: string;
    templateStructure?: any; // PDF模板结构信息
    extractedText?: string; // PDF提取的文本
  };
  regions?: TemplateRegion[];
}

interface TemplateRegion {
  id: string;
  name: string;
  inputRegion: {
    x: number;
    y: number;
    width: number;
    height: number;
  };
  outputCell?: string; // Excel单元格位置，如 "A1"
  fieldType: 'text' | 'number' | 'date' | 'currency';
}

interface TemplateManagerProps {
  onTemplateSelect: (template: Template | null) => void;
  selectedTemplate?: Template | null;
}

// 缺省模板定义
const DEFAULT_TEMPLATE: Template = {
  filename: 'default_template',
  originalname: '标准订单模板',
  size: 0,
  uploadTime: new Date().toISOString(),
  isDefault: true
};

const TemplateManager: React.FC<TemplateManagerProps> = ({ onTemplateSelect, selectedTemplate }) => {
  const [templates, setTemplates] = useState<Template[]>([]);
  const [uploading, setUploading] = useState(false);
  const [showTemplateEditor, setShowTemplateEditor] = useState(false);
  const [currentTemplate, setCurrentTemplate] = useState<Template | null>(null);
  const [inputFilePreview, setInputFilePreview] = useState<string | null>(null);
  const [isSelectingRegion, setIsSelectingRegion] = useState(false);
  const [selectedRegions, setSelectedRegions] = useState<TemplateRegion[]>([]);
  const [dragStart, setDragStart] = useState<{x: number, y: number} | null>(null);
  const [currentRegion, setCurrentRegion] = useState<Partial<TemplateRegion> | null>(null);
  
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const imageRef = useRef<HTMLImageElement>(null);

  // 获取模板列表
  const fetchTemplates = useCallback(async () => {
    try {
      const response = await fetch('/api/templates');
      const data = await response.json();
      if (data.templates) {
        const allTemplates = [DEFAULT_TEMPLATE, ...data.templates];
        setTemplates(allTemplates);
        
        if (!selectedTemplate) {
          onTemplateSelect(DEFAULT_TEMPLATE);
        }
      }
    } catch (error) {
      console.error('获取模板列表失败:', error);
    }
  }, [selectedTemplate, onTemplateSelect]);

  useEffect(() => {
    fetchTemplates();
  }, [fetchTemplates]);

  // 上传输入文件
  const uploadInputFile = useCallback(async (file: File) => {
    setUploading(true);
    try {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('uploadType', 'template_input');

      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      const result = await response.json();

      if (result.success) {
        const previewUrl = URL.createObjectURL(file);
        setInputFilePreview(previewUrl);
        
        setCurrentTemplate(prev => ({
          filename: `template_${Date.now()}`,
          originalname: '新建模板',
          size: file.size,
          uploadTime: new Date().toISOString(),
          ...prev,
          inputFile: {
            filename: result.file.filename,
            originalname: file.name,
            type: file.type,
            url: previewUrl
          }
        }));
        
        toast.success('输入文件上传成功！');
      } else {
        toast.error(result.error || '输入文件上传失败');
      }
    } catch (error) {
      console.error('输入文件上传错误:', error);
      toast.error('输入文件上传失败');
    } finally {
      setUploading(false);
    }
  }, []);

  // 上传输出模板文件
  const uploadOutputFile = useCallback(async (file: File) => {
    setUploading(true);
    try {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('uploadType', 'template_output');

      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      const result = await response.json();

      if (result.success) {
        setCurrentTemplate(prev => ({
          filename: prev?.filename || `template_${Date.now()}`,
          originalname: prev?.originalname || '新建模板',
          size: prev?.size || file.size,
          uploadTime: prev?.uploadTime || new Date().toISOString(),
          ...prev,
          outputFile: {
            filename: result.file.filename,
            originalname: file.name,
            type: file.type,
            templateStructure: result.file.templateStructure,
            extractedText: result.file.extractedText
          }
        }));
        
        if (result.file.templateStructure) {
          toast.success('PDF输出模板上传成功！已分析模板结构');
        } else {
          toast.success('输出模板上传成功！');
        }
      } else {
        toast.error(result.error || '输出模板上传失败');
      }
    } catch (error) {
      console.error('输出模板上传错误:', error);
      toast.error('输出模板上传失败');
    } finally {
      setUploading(false);
    }
  }, []);

  // 输入文件拖拽
  const inputDropzone = useDropzone({
    onDrop: (acceptedFiles) => {
      if (acceptedFiles.length > 0) {
        uploadInputFile(acceptedFiles[0]);
      }
    },
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.gif', '.bmp'],
      'application/pdf': ['.pdf']
    },
    maxFiles: 1,
    disabled: uploading
  });

  // 输出文件拖拽
  const outputDropzone = useDropzone({
    onDrop: (acceptedFiles) => {
      if (acceptedFiles.length > 0) {
        uploadOutputFile(acceptedFiles[0]);
      }
    },
    accept: {
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': ['.xlsx'],
      'application/vnd.ms-excel': ['.xls'],
      'application/json': ['.json'],
      'application/pdf': ['.pdf']
    },
    maxFiles: 1,
    disabled: uploading
  });

  // 开始区域选择
  const startRegionSelection = useCallback(() => {
    setIsSelectingRegion(true);
    toast('请在图片上拖拽选择识别区域', { icon: 'ℹ️' });
  }, []);

  // 画布鼠标事件处理
  const handleCanvasMouseDown = useCallback((e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!isSelectingRegion) return;
    
    const canvas = canvasRef.current;
    if (!canvas) return;

    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;
    
    setDragStart({ x, y });
  }, [isSelectingRegion]);

  const handleCanvasMouseMove = useCallback((e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!isSelectingRegion || !dragStart) return;

    const canvas = canvasRef.current;
    const ctx = canvas?.getContext('2d');
    if (!canvas || !ctx) return;

    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    // 重绘图像
    const image = imageRef.current;
    if (image) {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(image, 0, 0, canvas.width, canvas.height);
    }

    // 绘制当前选择框
    ctx.strokeStyle = '#3B82F6';
    ctx.lineWidth = 2;
    ctx.strokeRect(
      dragStart.x,
      dragStart.y,
      x - dragStart.x,
      y - dragStart.y
    );
  }, [isSelectingRegion, dragStart]);

  const handleCanvasMouseUp = useCallback((e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!isSelectingRegion || !dragStart) return;

    const canvas = canvasRef.current;
    if (!canvas) return;

    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    const width = Math.abs(x - dragStart.x);
    const height = Math.abs(y - dragStart.y);

    if (width > 10 && height > 10) {
      const newRegion: TemplateRegion = {
        id: `region_${Date.now()}`,
        name: `区域 ${selectedRegions.length + 1}`,
        inputRegion: {
          x: Math.min(dragStart.x, x),
          y: Math.min(dragStart.y, y),
          width,
          height
        },
        fieldType: 'text'
      };

      setSelectedRegions(prev => [...prev, newRegion]);
      toast.success(`已添加识别区域：${newRegion.name}`);
    }

    setDragStart(null);
    setIsSelectingRegion(false);
  }, [isSelectingRegion, dragStart, selectedRegions.length]);

  // 保存模板
  const saveTemplate = useCallback(async () => {
    if (!currentTemplate?.inputFile || !currentTemplate?.outputFile || selectedRegions.length === 0) {
      toast.error('请完成输入文件、输出文件上传和区域选择');
      return;
    }

    try {
      const templateData = {
        ...currentTemplate,
        regions: selectedRegions
      };

      const response = await fetch('/api/templates', { 
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ templateData })
      });

      const result = await response.json();

      if (result.success) {
        setTemplates(prev => [...prev, templateData]);
        toast.success('模板保存成功！');
        setShowTemplateEditor(false);
        setCurrentTemplate(null);
        setInputFilePreview(null);
        setSelectedRegions([]);
        await fetchTemplates(); // 重新获取模板列表
      } else {
        toast.error(result.error || '模板保存失败');
      }
    } catch (error) {
      console.error('保存模板错误:', error);
      toast.error('模板保存失败');
    }
  }, [currentTemplate, selectedRegions, fetchTemplates]);

  // 删除模板
  const deleteTemplate = useCallback(async (filename: string) => {
    if (!window.confirm('确定要删除这个模板吗？')) return;

    try {
      const response = await fetch(`/api/templates/${filename}`, {
        method: 'DELETE',
      });

      const result = await response.json();

      if (result.success) {
        toast.success('模板删除成功！');
        await fetchTemplates();
        if (selectedTemplate?.filename === filename) {
          onTemplateSelect(null);
        }
      } else {
        toast.error(result.error || '模板删除失败');
      }
    } catch (error) {
      console.error('删除模板错误:', error);
      toast.error('模板删除失败');
    }
  }, [fetchTemplates, onTemplateSelect, selectedTemplate]);

  // 绘制已选择的区域
  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas?.getContext('2d');
    const image = imageRef.current;
    
    if (!canvas || !ctx || !image || !inputFilePreview) return;

    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(image, 0, 0, canvas.width, canvas.height);

    // 绘制已选择的区域
    selectedRegions.forEach((region, index) => {
      ctx.strokeStyle = '#10B981';
      ctx.lineWidth = 2;
      ctx.strokeRect(
        region.inputRegion.x,
        region.inputRegion.y,
        region.inputRegion.width,
        region.inputRegion.height
      );

      // 添加区域标签
      ctx.fillStyle = '#10B981';
      ctx.fillRect(
        region.inputRegion.x,
        region.inputRegion.y - 20,
        60,
        20
      );
      ctx.fillStyle = 'white';
      ctx.font = '12px Arial';
      ctx.fillText(
        region.name,
        region.inputRegion.x + 5,
        region.inputRegion.y - 5
      );
    });
  }, [selectedRegions, inputFilePreview]);

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '系统内置';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  if (showTemplateEditor) {
    return (
      <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
        <div className="p-4 border-b border-gray-200">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-medium text-gray-900">创建新模板</h3>
            <button
              onClick={() => setShowTemplateEditor(false)}
              className="text-gray-400 hover:text-gray-600"
            >
              ✕
            </button>
          </div>
          <p className="text-sm text-gray-500 mt-1">
            上传输入文件和输出模板，设置识别区域和存储位置
          </p>
        </div>

        <div className="p-4 space-y-6">
          {/* 步骤1：上传输入文件 */}
          <div>
            <h4 className="text-sm font-medium text-gray-900 mb-2 flex items-center">
              <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full text-xs mr-2">1</span>
              上传输入文件
            </h4>
            <div
              {...inputDropzone.getRootProps()}
              className={`border-2 border-dashed rounded-lg p-4 text-center cursor-pointer transition-colors ${
                inputDropzone.isDragActive
                  ? 'border-blue-300 bg-blue-50'
                  : 'border-gray-300 hover:border-blue-400'
              }`}
            >
              <input {...inputDropzone.getInputProps()} />
              <PhotoIcon className="mx-auto h-8 w-8 text-gray-400 mb-2" />
              <p className="text-sm text-gray-600">
                拖拽或点击上传输入文件 (PDF, JPG 格式)
              </p>
            </div>
            {currentTemplate?.inputFile && (
              <p className="text-sm text-green-600 mt-2">
                ✓ 已上传：{currentTemplate.inputFile.originalname}
              </p>
            )}
          </div>

          {/* 步骤2：上传输出模板 */}
          <div>
            <h4 className="text-sm font-medium text-gray-900 mb-2 flex items-center">
              <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full text-xs mr-2">2</span>
              上传输出模板
            </h4>
            <div
              {...outputDropzone.getRootProps()}
              className={`border-2 border-dashed rounded-lg p-4 text-center cursor-pointer transition-colors ${
                outputDropzone.isDragActive
                  ? 'border-blue-300 bg-blue-50'
                  : 'border-gray-300 hover:border-blue-400'
              }`}
            >
              <input {...outputDropzone.getInputProps()} />
              <DocumentTextIcon className="mx-auto h-8 w-8 text-gray-400 mb-2" />
              <p className="text-sm text-gray-600">
                拖拽或点击上传输出模板 (Excel, JSON, PDF 格式)
              </p>
            </div>
            {currentTemplate?.outputFile && (
              <p className="text-sm text-green-600 mt-2">
                ✓ 已上传：{currentTemplate.outputFile.originalname}
              </p>
            )}
            
            {/* 显示PDF模板结构信息 */}
            {currentTemplate?.outputFile?.templateStructure && (
              <div className="mt-3 p-3 bg-blue-50 border border-blue-200 rounded-lg">
                <h5 className="text-sm font-medium text-blue-900 mb-2">PDF模板结构分析</h5>
                <div className="text-xs text-blue-800 space-y-1">
                  {currentTemplate.outputFile.templateStructure.sections?.length > 0 && (
                    <p>
                      <span className="font-medium">发现章节：</span>
                      {currentTemplate.outputFile.templateStructure.sections.map((section: any) => section.title).join(', ')}
                    </p>
                  )}
                  {currentTemplate.outputFile.templateStructure.headers?.length > 0 && (
                    <p>
                      <span className="font-medium">表格标题：</span>
                      {currentTemplate.outputFile.templateStructure.headers.map((header: any) => header.line).join('; ')}
                    </p>
                  )}
                  {currentTemplate.outputFile.templateStructure.suggestedMapping && (
                    <p>
                      <span className="font-medium">建议映射：</span>
                      商品名称→{currentTemplate.outputFile.templateStructure.suggestedMapping.productNameColumn}列,
                      数量→{currentTemplate.outputFile.templateStructure.suggestedMapping.quantityColumn}列,
                      单价→{currentTemplate.outputFile.templateStructure.suggestedMapping.unitPriceColumn}列
                    </p>
                  )}
                </div>
              </div>
            )}
          </div>

          {/* 步骤3：选择识别区域 */}
          {inputFilePreview && (
            <div>
              <h4 className="text-sm font-medium text-gray-900 mb-2 flex items-center">
                <span className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full text-xs mr-2">3</span>
                选择识别区域
              </h4>
              
              <div className="border rounded-lg p-4">
                <div className="mb-4 flex items-center justify-between">
                  <button
                    onClick={startRegionSelection}
                    className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center space-x-2"
                  >
                    <PlusIcon className="h-4 w-4" />
                    <span>添加识别区域</span>
                  </button>
                  <span className="text-sm text-gray-500">
                    已选择 {selectedRegions.length} 个区域
                  </span>
                </div>

                <div className="relative">
                  <img
                    ref={imageRef}
                    src={inputFilePreview}
                    alt="Input file preview"
                    className="max-w-full h-auto"
                    onLoad={(e) => {
                      const img = e.target as HTMLImageElement;
                      const canvas = canvasRef.current;
                      if (canvas) {
                        canvas.width = img.naturalWidth;
                        canvas.height = img.naturalHeight;
                        canvas.style.width = `${img.offsetWidth}px`;
                        canvas.style.height = `${img.offsetHeight}px`;
                      }
                    }}
                  />
                  <canvas
                    ref={canvasRef}
                    className="absolute top-0 left-0 cursor-crosshair"
                    onMouseDown={handleCanvasMouseDown}
                    onMouseMove={handleCanvasMouseMove}
                    onMouseUp={handleCanvasMouseUp}
                  />
                </div>
              </div>

              {/* 区域列表 */}
              {selectedRegions.length > 0 && (
                <div className="mt-4">
                  <h5 className="text-sm font-medium text-gray-900 mb-2">识别区域列表</h5>
                  <div className="space-y-2">
                    {selectedRegions.map((region, index) => (
                      <div key={region.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                        <div className="flex items-center space-x-3">
                          <RectangleStackIcon className="h-5 w-5 text-green-500" />
                          <div>
                            <p className="text-sm font-medium">{region.name}</p>
                            <p className="text-xs text-gray-500">
                              位置: ({region.inputRegion.x}, {region.inputRegion.y}) 
                              大小: {region.inputRegion.width}×{region.inputRegion.height}
                            </p>
                          </div>
                        </div>
                        <div className="flex items-center space-x-2">
                          <ArrowRightIcon className="h-4 w-4 text-gray-400" />
                          <input
                            type="text"
                            placeholder="Excel单元格(如A1)"
                            value={region.outputCell || ''}
                            onChange={(e) => {
                              const updatedRegions = [...selectedRegions];
                              updatedRegions[index].outputCell = e.target.value;
                              setSelectedRegions(updatedRegions);
                            }}
                            className="w-24 px-2 py-1 text-xs border rounded"
                          />
                          <button
                            onClick={() => {
                              setSelectedRegions(prev => prev.filter(r => r.id !== region.id));
                            }}
                            className="text-red-500 hover:text-red-700"
                          >
                            <TrashIcon className="h-4 w-4" />
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}

          {/* 保存按钮 */}
          <div className="flex justify-end space-x-3">
            <button
              onClick={() => setShowTemplateEditor(false)}
              className="px-4 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200"
            >
              取消
            </button>
            <button
              onClick={saveTemplate}
              disabled={!currentTemplate?.inputFile || !currentTemplate?.outputFile || selectedRegions.length === 0}
              className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              保存模板
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
      <div className="p-4 border-b border-gray-200">
        <div className="flex items-center justify-between">
          <div>
            <h3 className="text-lg font-medium text-gray-900">模板管理</h3>
            <p className="text-sm text-gray-500 mt-1">
              管理识别模板，设置输入区域和输出位置映射
            </p>
          </div>
          <button
            onClick={() => setShowTemplateEditor(true)}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center space-x-2"
          >
            <PlusIcon className="h-4 w-4" />
            <span>创建模板</span>
          </button>
        </div>
      </div>

      <div className="p-4">
        {/* 模板列表 */}
        {templates.length > 0 && (
          <div className="mb-4">
            <h4 className="text-sm font-medium text-gray-900 mb-2">可用模板</h4>
            <div className="space-y-2 max-h-48 overflow-y-auto">
              {templates.map((template) => (
                <div
                  key={template.filename}
                  className={`flex items-center justify-between p-3 border rounded-lg cursor-pointer transition-colors ${
                    selectedTemplate?.filename === template.filename
                      ? 'border-primary-500 bg-primary-50'
                      : 'border-gray-200 hover:border-gray-300'
                  }`}
                  onClick={() => onTemplateSelect(template)}
                >
                  <div className="flex items-center space-x-3">
                    {template.isDefault ? (
                      <CheckCircleIcon className="h-5 w-5 text-green-500" />
                    ) : (
                      <DocumentTextIcon className="h-5 w-5 text-gray-400" />
                    )}
                    <div>
                      <p className="text-sm font-medium text-gray-900 flex items-center space-x-2">
                        <span>{template.originalname}</span>
                        {template.isDefault && (
                          <span className="px-2 py-1 text-xs bg-green-100 text-green-800 rounded-full">
                            缺省
                          </span>
                        )}
                        {template.regions && (
                          <span className="px-2 py-1 text-xs bg-blue-100 text-blue-800 rounded-full">
                            {template.regions.length}个区域
                          </span>
                        )}
                      </p>
                      <p className="text-xs text-gray-500">
                        {formatFileSize(template.size)} • {template.isDefault ? '系统内置模板' : `上传于 ${new Date(template.uploadTime).toLocaleDateString()}`}
                      </p>
                    </div>
                  </div>
                  {!template.isDefault && (
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        deleteTemplate(template.filename);
                      }}
                      className="p-1 text-gray-400 hover:text-red-500 transition-colors"
                    >
                      <TrashIcon className="h-4 w-4" />
                    </button>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}

        {selectedTemplate && (
          <div className="mt-4 p-3 bg-green-50 border border-green-200 rounded-lg">
            <p className="text-sm text-green-800">
              <span className="font-medium">当前模板：</span>
              {selectedTemplate.originalname}
              {selectedTemplate.isDefault && <span className="ml-2 text-xs">(系统自动配置)</span>}
            </p>
            {selectedTemplate.regions && (
              <p className="text-xs text-green-700 mt-1">
                包含 {selectedTemplate.regions.length} 个识别区域
              </p>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default TemplateManager; 