import React, { useState, useCallback } from 'react';
import * as XLSX from 'xlsx';
import { toast } from 'react-hot-toast';
import { RecognizedData } from '../types';

interface Template {
  filename: string;
  originalname: string;
  size: number;
  uploadTime: string;
  isDefault?: boolean;
}

interface ExcelGeneratorProps {
  data: RecognizedData;
  originalImage?: File | null;
  selectedTemplate?: Template | null;
  onReset?: () => void;
}

const ExcelGenerator: React.FC<ExcelGeneratorProps> = ({ 
  data, 
  originalImage, 
  selectedTemplate,
  onReset 
}) => {
  const [isGenerating, setIsGenerating] = useState(false);

  // 生成Excel文件
  const generateExcel = useCallback(async () => {
    setIsGenerating(true);
    
    try {
      const workbook = XLSX.utils.book_new();
      
      // 根据模板类型决定数据格式
      const isUsingDefaultTemplate = selectedTemplate?.isDefault;
      
      if (isUsingDefaultTemplate) {
        // 使用缺省模板格式 - 更规范的订单格式
        const worksheetData = [
          // 标题行
          ['订单信息表', '', '', '', ''],
          ['', '', '', '', ''],
          // 基本信息部分
          ['基本信息', '', '', '', ''],
          ['订单号', data.orderNumber, '', '供应商', data.supplier],
          ['订单日期', data.orderDate, '', '交付日期', data.deliveryDate || ''],
          ['', '', '', '', ''],
          // 商品明细部分
          ['商品明细', '', '', '', ''],
          ['序号', '商品名称', '数量', '单价(元)', '小计(元)'],
          ...data.items.map((item, index) => [
            index + 1,
            item.productName,
            item.quantity,
            item.unitPrice,
            item.totalPrice
          ]),
          ['', '', '', '', ''],
          // 汇总信息
          ['汇总信息', '', '', '', ''],
          ['商品种类', data.items.length + '种', '', '总数量', data.items.reduce((sum, item) => sum + item.quantity, 0)],
          ['总金额', data.totalAmount.toFixed(2) + '元', '', '', ''],
          ['', '', '', '', ''],
          ['生成时间', new Date().toLocaleString(), '', '', '']
        ];

        const worksheet = XLSX.utils.aoa_to_sheet(worksheetData);
        
        // 设置列宽
        worksheet['!cols'] = [
          { width: 12 }, // 序号/标签列
          { width: 25 }, // 名称列
          { width: 12 }, // 数量列
          { width: 15 }, // 单价列
          { width: 15 }  // 小计列
        ];

        // 设置样式（虽然XLSX基础版本支持有限，但设置了列宽）
        XLSX.utils.book_append_sheet(workbook, worksheet, '订单明细');
      } else {
        // 使用自定义模板或原有格式
        const worksheetData = [
          ['订单信息', '', '', ''],
          ['订单号', data.orderNumber, '', ''],
          ['供应商', data.supplier, '', ''],
          ['订单日期', data.orderDate, '', ''],
          ['交付日期', data.deliveryDate || '', '', ''],
          ['', '', '', ''],
          ['商品清单', '', '', ''],
          ['商品名称', '数量', '单价', '小计'],
          ...data.items.map(item => [
            item.productName,
            item.quantity,
            item.unitPrice,
            item.totalPrice
          ]),
          ['', '', '', ''],
          ['总金额', '', '', data.totalAmount.toFixed(2)]
        ];

        const worksheet = XLSX.utils.aoa_to_sheet(worksheetData);
        
        // 设置列宽
        worksheet['!cols'] = [
          { width: 20 },
          { width: 15 },
          { width: 15 },
          { width: 15 }
        ];

        XLSX.utils.book_append_sheet(workbook, worksheet, '订单明细');
      }

      // 添加模板信息工作表
      if (selectedTemplate) {
        const templateInfo = [
          ['使用的模板信息', ''],
          ['模板名称', selectedTemplate.originalname],
          ['模板类型', selectedTemplate.isDefault ? '系统缺省模板' : '用户自定义模板'],
          ['应用时间', new Date().toLocaleString()],
          ...(selectedTemplate.isDefault ? [] : [
            ['上传时间', new Date(selectedTemplate.uploadTime).toLocaleString()],
            ['文件大小', `${(selectedTemplate.size / 1024).toFixed(2)} KB`]
          ])
        ];
        
        const templateSheet = XLSX.utils.aoa_to_sheet(templateInfo);
        XLSX.utils.book_append_sheet(workbook, templateSheet, '模板信息');
      }

      // 添加原始文本工作表
      if (data.rawText) {
        const rawTextData = [
          ['原始识别文本'],
          [''],
          [data.rawText]
        ];
        const rawTextSheet = XLSX.utils.aoa_to_sheet(rawTextData);
        XLSX.utils.book_append_sheet(workbook, rawTextSheet, '原始文本');
      }

      // 生成文件名
      const templatePrefix = selectedTemplate?.isDefault ? '标准_' : (selectedTemplate ? '模板_' : '');
      const fileName = `${templatePrefix}订单_${data.orderNumber}_${new Date().toLocaleDateString().replace(/\//g, '-')}.xlsx`;
      
      // 导出文件
      XLSX.writeFile(workbook, fileName);
      
      toast.success(`Excel文件生成成功！${selectedTemplate?.isDefault ? '(使用系统标准格式)' : ''}`);
    } catch (error) {
      console.error('Excel生成错误:', error);
      toast.error('Excel文件生成失败');
    } finally {
      setIsGenerating(false);
    }
  }, [data, selectedTemplate]);

  // 生成JSON格式数据
  const generateJSON = useCallback(() => {
    try {
      const jsonData = {
        ...data,
        template: selectedTemplate ? {
          name: selectedTemplate.originalname,
          uploadTime: selectedTemplate.uploadTime
        } : null,
        exportTime: new Date().toISOString(),
        originalFileName: originalImage?.name || ''
      };

      const dataStr = JSON.stringify(jsonData, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      
      const url = URL.createObjectURL(dataBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `订单数据_${data.orderNumber}_${new Date().toLocaleDateString().replace(/\//g, '-')}.json`;
      
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      URL.revokeObjectURL(url);
      toast.success('JSON文件导出成功！');
    } catch (error) {
      console.error('JSON导出错误:', error);
      toast.error('JSON文件导出失败');
    }
  }, [data, selectedTemplate, originalImage]);

  // 计算统计信息
  const stats = {
    itemCount: data.items.length,
    totalQuantity: data.items.reduce((sum, item) => sum + item.quantity, 0),
    averagePrice: data.items.length > 0 
      ? data.items.reduce((sum, item) => sum + item.unitPrice, 0) / data.items.length 
      : 0
  };

  return (
    <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
      <div className="p-4 border-b border-gray-200">
        <h3 className="text-lg font-medium text-gray-900">数据导出</h3>
        <p className="text-sm text-gray-500 mt-1">
          生成Excel表格或JSON格式数据
        </p>
      </div>

      <div className="p-4">
        {/* 数据统计 */}
        <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="bg-blue-50 p-3 rounded-lg">
            <div className="text-xs text-blue-600 font-medium">商品种类</div>
            <div className="text-lg font-bold text-blue-900">{stats.itemCount}</div>
          </div>
          <div className="bg-green-50 p-3 rounded-lg">
            <div className="text-xs text-green-600 font-medium">总数量</div>
            <div className="text-lg font-bold text-green-900">{stats.totalQuantity}</div>
          </div>
          <div className="bg-purple-50 p-3 rounded-lg">
            <div className="text-xs text-purple-600 font-medium">平均单价</div>
            <div className="text-lg font-bold text-purple-900">¥{stats.averagePrice.toFixed(2)}</div>
          </div>
        </div>

        {/* 模板信息 */}
        {selectedTemplate && (
          <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg">
            <h4 className="text-sm font-medium text-green-800 mb-2">使用的模板</h4>
            <p className="text-sm text-green-700">
              <span className="font-medium">模板名称：</span>
              {selectedTemplate.originalname}
            </p>
            <p className="text-sm text-green-700">
              <span className="font-medium">上传时间：</span>
              {new Date(selectedTemplate.uploadTime).toLocaleString()}
            </p>
          </div>
        )}

        {/* 数据预览 */}
        <div className="mb-6">
          <h4 className="text-sm font-medium text-gray-900 mb-3">数据预览</h4>
          <div className="border border-gray-200 rounded-lg overflow-hidden">
            <div className="bg-gray-50 px-4 py-2 border-b border-gray-200">
              <div className="grid grid-cols-4 gap-4 text-xs font-medium text-gray-700">
                <div>商品名称</div>
                <div>数量</div>
                <div>单价</div>
                <div>小计</div>
              </div>
            </div>
            <div className="max-h-48 overflow-y-auto">
              {data.items.map((item, index) => (
                <div key={index} className="px-4 py-2 border-b border-gray-100 last:border-b-0">
                  <div className="grid grid-cols-4 gap-4 text-sm">
                    <div className="truncate">{item.productName}</div>
                    <div>{item.quantity}</div>
                    <div>¥{item.unitPrice.toFixed(2)}</div>
                    <div className="font-medium">¥{item.totalPrice.toFixed(2)}</div>
                  </div>
                </div>
              ))}
            </div>
            <div className="bg-blue-50 px-4 py-2 border-t border-blue-200">
              <div className="flex justify-between items-center text-sm">
                <span className="font-medium text-blue-900">总金额</span>
                <span className="text-lg font-bold text-blue-900">¥{data.totalAmount.toFixed(2)}</span>
              </div>
            </div>
          </div>
        </div>

        {/* 导出按钮 */}
        <div className="space-y-3">
          <button
            onClick={generateExcel}
            disabled={isGenerating}
            className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            {isGenerating ? (
              <>
                <svg className="animate-spin w-4 h-4" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                <span>生成中...</span>
              </>
            ) : (
              <>
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
                <span>下载Excel文件</span>
              </>
            )}
          </button>

          <button
            onClick={generateJSON}
            className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7v8a2 2 0 002 2h6M8 7V5a2 2 0 012-2h4.586a1 1 0 01.707.293l4.414 4.414a1 1 0 01.293.707V15a2 2 0 01-2 2v0a2 2 0 01-2-2v-2M8 7H6a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2v-2" />
            </svg>
            <span>导出JSON数据</span>
          </button>

          {onReset && (
            <button
              onClick={onReset}
              className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              <span>重新开始</span>
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default ExcelGenerator; 